import React, { Component } from 'react'
import { connect } from 'react-redux'
import Pager from 'react-pager'
import TasksList from '../components/TasksList'
import SortTasks from '../components/SortTasks'
import { getTasks, sortBy } from '../actionCreators/actionCreators'
import { createPaginate } from '../helpers'

class Tasks extends Component {

  state = {
    sortValue: '',
    editing: false,
    total: 1,
    current: 1,
    visiblePage: 3,
    rowsPaginate: []
  }

  componentDidMount() {
    const { tasks } = this.props
    const { current } = this.state
    this.setState({
      rowsPaginate: createPaginate(current, tasks),
      total: tasks.length / 3
    })
  }

  componentDidUpdate(prevProps) {
    const { tasks } = this.props
    const { current } = this.state

    if (prevProps.tasks.length < tasks.length)
      this.setState({
        rowsPaginate: createPaginate(current, tasks),
        total: Math.ceil(tasks.length / 3)
      })
  }

  handlePageChanged = newPage => {
      const { tasks } = this.props
      const { current } = this.state

      this.setState({
        rowsPaginate: createPaginate(newPage + 1, tasks),
        current : newPage + 1
      })
    }

  onChange = ev => {
    const value = ev.target.value
    this.setState({sortValue: value})
  }

  done = ev => {
    console.log(ev.target.id);
  }

  edit = () => {
    this.setState({editing: !this.state.editing})
  }

  sortByVal = () => {
    const { sortValue } = this.state
    if (sortValue) this.props.dispatch(sortBy(sortValue))

    this.setState({sortValue: ''})
  }

  render() {
    const { tasks, isLoad, isErr, adminMode } = this.props
    const { sortValue, visiblePage, current, editing, rowsPaginate, total } = this.state

      return (
        <section className="task-container">
        <SortTasks sortValue={sortValue} sortByVal={this.sortByVal} onChange={this.onChange}/>
          <TasksList
            tasks={rowsPaginate}
            isLoad={isLoad}
            isErr={isErr}
            adminMode={adminMode}
            done={this.done}
            edit={this.edit}
            editing={editing}
          />
          <Pager
            total={Math.ceil(total)}
            current={current - 1}
            visiblePages={visiblePage}
            titles={{ first: '<|', last: '>|' }}
            className="pagination-sm"
            onPageChanged={this.handlePageChanged}
          />
        </section>
    );
  }
}

const mapStateToProps = state => {
  return {
    isLoad: state.main.load,
    isErr: state.main.err,
  }
}
export default connect(mapStateToProps)(Tasks)



